<template>
  <div>
    <div class="headerPosition">
      <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
        <a @click="()=>{
          $router.go(-1)
        }">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span style="color:white">{{title}}</span>
      </mu-appbar>-->
      <van-nav-bar
       
        :title="title"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="999"
        :border="false"
      >
        <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
      </van-nav-bar>
      <div class="body" style="padding-bottom:0px">
        <div class="headerBack">
          <div class="flexBox headerBlueBack size backgroundNaviBar">
            <div>
              <span>当前设备状态</span>
            </div>
            <div class="statusButton" :style="systemStatus===1?itemColor:noColor">
              <!-- <span>停运</span> -->
              <span v-show="systemStatus===1">正常</span>
              <span v-show="systemStatus===0">异常</span>
              <span v-show="systemStatus===2">维护</span>
              <span v-show="systemStatus===3">离线</span>
              <span v-show="systemStatus===4">无水</span>
              <span v-show="systemStatus===8">停运</span>
            </div>
          </div>
          <div class="btnDevice flexBox boxHeight">
            <div class="deviceButton" style="color:#217fff" @click="toSubmit(0,title,)">
              <span>设备停运</span>
            </div>
            <div style="flex:1"></div>
            <div class="deviceButton" @click="toSubmit(1,title)">
              <span>设备恢复</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="body margin-topHeader" style="padding-bottom:0">
      <div class="headerBack padding">
        <div class="deviceHeight">
          <span>设备状态记录</span>
        </div>
        <!-- <div v-for="(item,index) in itemList" :key="index">
          <div class="Border-Top">
            <div></div>
          </div>
        </div>-->
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
            class="Border-Top"
          >
            <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
            <div v-for="(item,index) in list" :key="index">
              <div class="Border-Top font-sizeContent">
                <div>
                  <div class="heightSize">{{item.gmtCreate}}</div>
                  <div class="heightSize">
                    <span>操作人：</span>
                    <span>{{item.person}}</span>
                  </div>
                  <div class="heightSize">
                    <span>操作状态：</span>
                    <span>{{item.pid!==null?'停运→恢复':'恢复→停运'}}</span>
                  </div>
                  <div class="heightSize">
                    <span>原因：</span>
                    <span>{{item.reason}}</span>
                  </div>
                  <div class="heightSize">
                    <span>图片</span>
                  </div>
                  <div class="flex-wrap">
                    <div v-for="(item,index) in 3" :key="index" style="margin-right:5px;">
                      <van-image
                        width="5rem"
                        height="5rem"
                        fit="cover"
                        src="https://img.yzcdn.cn/vant/cat.jpeg"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { List, PullRefresh } from "vant";
import { Image } from "vant";

Vue.use(Image);

Vue.use(List).use(PullRefresh);
export default {
  data() {
    return {
      itemList: [1, 1, 1],
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      token: "",
      systemStatus: 0,
      itemColor: "color:blue",
      noColor: "color:red",
      title: ""
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
    this.systemStatus = this.$route.query.status;
    this.title = this.$route.query.station;
    this.initListData();
  },
  mounted() {},

  methods: {
    async initListData() {
      var url = "/job/operate/device";
      var search = this.$route.query.station;
      var res = await AppBace.request(
        url,
        "GET",
        {
          search
        },
        this.token
      );
      console.log(res);
      this.list = res.data.data;
      this.finished = true;
    },
    toSubmit(val, title) {
      //   var router = "";
      //   debugger;
      //   switch (val) {
      //     case 0:
      //       router = "DeviceSubmit";
      //       break;

      //     default:
      //       break;
      //   }
      this.$router.push({
        path: "DeviceSubmit",
        query: {
          status: val,
          title: title,
          id: this.list[0].id
        }
      });
    },
    onLoad() {
      // setTimeout(() => {
      this.initListData();
      if (this.refreshing) {
        this.list = [];
        this.refreshing = false;
      }

      // for (let i = 0; i < 10; i++) {
      //   this.list.push(this.list.length + 1);
      // }
      this.loading = false;

      // if (this.list.length === 40) {

      // }
      // }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    }
  }
};
</script>
<style  scoped>
.body {
  padding: 10px;
  background: rgb(245, 245, 245);
}
.flexBox {
  display: flex;
  justify-content: space-between;
}
.headerBack {
  background-color: white;
  border-radius: 2px;
  /* padding: 10px; */
}
.headerBlueBack {
  background-color: #2686ff;
  /* background: -webkit-linear-gradient(to left, #328efb, #4abbfe) !important;
  background: linear-gradient(to left, #328efb, #4abbfe) !important; */
  /* color: white; */
  color: white;
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
}
.size {
  padding: 0 10px;
  height: 40px;
  align-items: center;
}
.margin-top {
  margin-top: 10px;
}
.statusButton {
  background-color: white;
  border-radius: 10px;
  color: #ee002d;
  font-size: 12px;
  padding: 2px 10px;
}
.boxHeight {
  height: 80px;
  padding: 10px;
  align-items: center;
}
.deviceButton {
  box-shadow: 0 0 20px #ccc;
  flex: 2;
  height: 30px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0;
  font-size: 12px;
}
.padding {
  padding: 10px;
}
.Border-Top {
  border-top: 1px rgb(240, 237, 237) solid;
}
.deviceHeight {
  height: 40px;
  display: flex;
  align-items: center;
}
.headerPosition {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}
.margin-topHeader {
  margin-top: 176px;
}
.font-sizeContent {
  font-size: 12px;
  color: #999999;
}
.heightSize {
  height: 26px;
  display: flex;
  align-items: center;
}
.flex-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
</style>


